























import {DatetimePicker, Popup, Button, Field} from 'vant';
import Vue from "vue";
import {Component} from "vue-property-decorator";
import moment from 'moment'


@Component({
  components: {
    [DatetimePicker.name]: DatetimePicker,
    [Popup.name]: Popup,
    [Button.name]: Button,
    [Field.name]: Field
  }
})
export default class Detail extends Vue {
  currentDate = new Date();
  minDate = new Date(2021, 0, 1);
  maxDate = new Date(2021, 11, 31);
  show = false;
  msg = '';
  timeValue = '今天';

  get dateTime():string{
    const today=moment(new Date()).format('yyyy-MM-DD');
    const dateTime=this.$store.getters.getDate
    return dateTime===today? '今天':dateTime
  }



  showPopFn():void {
    this.show = true;
  }




  formatter(type:string, val:string):string {
    if (type === 'year') {
      return val + '年';
    }
    if (type === 'month') {
      return val + '月';
    }
    if (type === 'day') {
      return val + '日';
    }
    return val;
  }

  confirmFn():void { // 确定按钮
    const _time=moment(this.currentDate).format('yyyy-MM-DD');
    this.$store.commit('updateRecord',{date:_time});
    this.show = false;
  }

  cancelFn() :void{
    this.show = false;
  }

}
